const debugEnable = true;


/**
 * 
 * 
 * 
 * CattleBridge ÈÄâÈ°πÂàóË°®
 * OptionsÔºö
 * 
 * filters: [Object] Áî®‰∫éËøáÊª§ÁöÑÊï∞ÊçÆÂ§ÑÁêÜÂô®
 *      handler Ëá™ÂÆö‰πâÂ§ÑÁêÜÂô®
 *      chop ÂØπËæìÂÖ•Êï∞ÊçÆÁöÑÂ§ÑÁêÜ Function/Array[Function]
 *          chop (inp)
 *              Êé•ÂèóËæìÂÖ•Êï∞ÊçÆÔºåËøîÂõûÂÄº‰∏∫‰º†ÈÄÅÂà∞requsterÁöÑÊï∞ÊçÆ
 *      trim ÂØπËæìÂá∫Êï∞ÊçÆÁöÑÂ§ÑÁêÜ Function/Array[Function]
 *          trim (rep)
 *              Êé•ÂèóËøîÂõûÁöÑÊï∞ÊçÆÔºåËæìÂá∫ËæìÂá∫Êï∞ÊçÆ
 *      inputProcessor Êé•Êî∂Êï∞ÊçÆËøõË°åÂ§ÑÁêÜÔºåËøîÂõûrequesterÊé•ÂèóÁöÑÂèÇÊï∞ÂØπË±° Function
 *          inputProcessor(rawInp, choppedInpData)
 *          ËøîÂõûÁöÑÂèÇÊï∞ÂØπË±°‰ºöÂíåÁªèfilterÂ§ÑÁêÜÂêéÁöÑÂÖ∂‰ªñÂÄº‰ª•ÂèäÈªòËÆ§ÂÄºËøõË°åÂêàÂπ∂
 *          Ê≠§ÂáΩÊï∞ËøîÂõûÁöÑÂèÇÊï∞ÂÄºÂÖ∑ÊúâÊúÄ‰ΩéÁöÑ‰ºòÂÖàÁ∫ß„ÄÇ‰πüÂ∞±ÊòØËØ¥ÔºåfilterÈ°πÊåáÂÆöÁöÑÂÖ∂‰ªñÂèÇÊï∞ÂÄºÂèØ‰ª•‰ºòÂÖà‰∫éinputProcessorËÆ°ÁÆóÂá∫ÁöÑÂèÇÊï∞ÂÄº‰º†ÂÖ•requester‰∏≠„ÄÇ
 * 
 *      name Ê≠§ËØ∑Ê±ÇÁöÑ‰∏™ÊÄßÂåñË°®Ëø∞Ôºå‰ºöÂá∫Áé∞Âú®debugËæìÂá∫‰∏≠
 *      method ÊñπÊ≥ïÔºà‰∏ç‰ºöËøõË°åÂ§ÑÁêÜÔºâ
 *      url ÊîØÊåÅÂèÇÊï∞‰ª£ÂÖ•ÂäüËÉΩ String/Function
 *          url(inp)
 *      
 * debug: ÂêØÁî®Ë∞ÉËØïÊ®°Âºè
 * 
 * stater: Â§ÑÁêÜÁä∂ÊÄÅÂèÇÈáè
 * 
 * requester: ËØ∑Ê±ÇÂèëÈÄÅÂô®(ÂèÇÁÖß axio Êù•ÂÆûÁé∞)
 * 
 * gtrim: ÈíàÂØπÊé•Êî∂Êï∞ÊçÆÁöÑÂÖ®Â±ÄÈ¢ÑÂ§ÑÁêÜÂáΩÊï∞
 * 
 * gchop: ÈíàÂØπÂèëÈÄÅÊï∞ÊçÆÁöÑÂÖ®Â±ÄÁªàÂ§ÑÁêÜÂáΩÊï∞
 * 
 * 
 * 
 */

/*
 * * * * Áä∂ÊÄÅÂà§Êñ≠ÂõûË∞É * * * *
 * ËæìÂÖ•ÔºöÂìçÂ∫î‰Ωì„ÄÅHTTPÁä∂ÊÄÅ
 * ËæìÂá∫‰∏Ä‰∏™Â≠óÂÖ∏ÔºöÊàêÂäüËøòÊòØÂ§±Ë¥•result„ÄÅÁä∂ÊÄÅÁ†Åcode„ÄÅÁä∂ÊÄÅÊ∂àÊÅØmsg„ÄÅÂèãÂ•ΩÁöÑÁä∂ÊÄÅÊ∂àÊÅØfriMsg
 * 
*/

import devPrint from './printError' // Ë∞ÉËØï‰ø°ÊÅØËæìÂá∫Ê®°Âùó
import dataPipeProcess from './dataPipeProcess' // Êï∞ÊçÆÁÆ°ÈÅìÂºèÂ§ÑÁêÜÊ®°Âùó
import * as libParams from './params' // ÂØºÂÖ•Â∏∏ÈáèÂèÇÊï∞
import printError from './printError'
import processFilter from './processFilter'
import processResponse from './processResponse'
import { FilterFunc } from './interfaces/FilterFunc';
import { Filter } from './interfaces/Filter';


interface InputProcessFunction {
  (inp: any, ...cb: any[]): any
}

interface Filters {
  [prop: string]: Filter;
}

interface StaterFunction {
  (cb: Function, data: any, resp: any, filter: Filter): any
}

interface ConstructOptions {
  stater: StaterFunction;
  requester: (requestOptions: any) => any;
  debug?: boolean;
  filters?: Filters;
  gtrim?: FilterFunc;
  gchop?: FilterFunc;

}

function isFunction (fn) {
  return Object.prototype.toString.call(fn) == '[object Function]';
}


class CattleBridge {
  protected filters: Filters; // ËøáÊª§Âô®ÂàóË°®
  protected debug: boolean; // Ë∞ÉËØïÊ®°Âºè
  protected stater: StaterFunction; // Stater Ëé∑ÂèñÂìçÂ∫îÁöÑstaterËøîÂõû
  protected requester: Function; // ËØ∑Ê±ÇÂèëÈÄÅÂô® ÊåâÁÖßaxiosÁöÑAPIËøõË°åË∞ÉÁî®
  protected gtrim: FilterFunc; // ÂÖ®Â±ÄtrimÂ§ÑÁêÜ
  protected gchop: FilterFunc; // ÂÖ®Â±ÄchopÂ§ÑÁêÜ

  constructor (options: ConstructOptions) {
    this.filters = options.filters
    this.debug = !!options.debug
    this.stater = options.stater
    this.requester = options.requester
    this.gtrim = options.gtrim
    this.gchop = options.gchop
    
    // Ê†°È™åÂàùÂßãÂåñÂèÇÊï∞ÁöÑÊúâÊïàÊÄß   // Map type later, maybe.
    if (!(this.filters instanceof Object)) {
      devPrint('throw', "‚ùå Invalid Options: filters list");
    }
    if (!this.filters) {
      devPrint('throw', "‚ùå Invalid Options: filters list are expected to be an object");
    }
    if(!isFunction(this.stater)) {
      devPrint('throw', "‚ùå Invalid Options: stater is expected to be a Function");
    }
  }

  // ÂèëËµ∑ËØ∑Ê±Ç
  fetch (name: string, input?: any) {

    if (!(name in this.filters)) {
      devPrint('throw', "‚ùå Invalid param `name`: no matched filter in filter list.");
      return false;
    }

    let filter = this.filters[name];
    // ÊâìÂç∞Ë∞ÉËØï‰ø°ÊÅØ
    this.debug && devPrint('info', `‚åõ [ÂèëËµ∑ËØ∑Ê±Ç] È°πÁõÆÂêç ${name}\n`, `ËæìÂÖ•ÂèÇÊï∞:`, input)
  
    // Â¶ÇÊûúÁî®Êà∑Ëá™ÂÆö‰πâ‰∫ÜÂ§ÑÁêÜÂáΩÊï∞
    if (isFunction(filter.handler)) {
  
      return new Promise ((resolve, reject) => filter.handler(resolve, reject, name, input))
  
    } else {  // Ê≤°ÊúâËá™ÂÆö‰πâÂáΩÊï∞
      return new Promise ((resolve, reject) => {
        // Â§ÑÁêÜFilter
        let requestParams: any = processFilter.call(this, filter, input, this.gchop)
        // DEV
        console.log(requestParams)

        this.requester(requestParams)
        // Â¶ÇÊûúHTTPÊ≠£Â∏∏ÂìçÂ∫î
        .then((rawStat: any) => {
          this.debug && printError('info', 'üåê ËØ∑Ê±ÇÊ≠£Â∏∏')
          processResponse.call(this, rawStat, undefined, filter, this.gtrim, this.stater, resolve, reject)
        // AJAXÂèëÁîüÈîôËØØ
        }, (error: any) => {
          this.debug && printError('info', 'üåê ËØ∑Ê±ÇÈîôËØØ\n', 'error: ', error);
          processResponse.call(this, error.response, error, filter, this.gtrim, this.stater, resolve, reject)
        })
      })
    }
  }
}


// Ê®°ÂùóËæìÂá∫
export default CattleBridge
